import * as store from 'store'

import { h, Component } from 'preact';
import MessageArea from './message-area';

let POLLING_INTERVAL = 10
let TIMER
let NUM_TRIES = 0
export default class Chat extends Component {

    autoResponseState = 'pristine'; // pristine, set or canceled
    autoResponseTimer = 0;

    constructor(props) {
        super(props);
        if (store.enabled) {
            this.messagesKey = 'messages' + '.' + props.chatId + '.' + props.host;
            this.state.messages = store.get(this.messagesKey) || store.set(this.messagesKey, []);
        } else {
            this.state.messages = [];
        }
        this.everlifeAvatar = props.everlifeAvatar
        this.pollForMsgs()
    }

    componentDidMount() {
        this.pollForMsgs()
    }

    render({},state) {
        return (
            <div>
                <MessageArea messages={state.messages} conf={this.props.conf}/>

                <input class="textarea" type="text" placeholder={this.props.conf.placeholderText}
                       ref={(input) => { this.input = input }}
                       id='everlifeuserchatinput'
                       onKeyPress={this.handleKeyPress}/>    
            </div>
        );
    }

    handleKeyPress = (e) => {
        if (e.keyCode == 13 && this.input.value) {
            let text = this.input.value;
            this.sendMessageToBot(text)
            this.input.value = '';
            
            this.writeToMessages({
                text:text,
                from:'visitor'
            })
            this.gotMsg()

            if (this.autoResponseState === 'pristine') {

                setTimeout(() => {
                }, 500);

                this.autoResponseTimer = setTimeout(() => {
                    
                    this.autoResponseState = 'canceled';
                }, 60 * 1000);
                
                this.autoResponseState = 'set';
            }
        }
    };



currentTimeOut = () => {
    if(POLLING_INTERVAL < 10) {
        NUM_TRIES++
        if(NUM_TRIES > 30) {
            POLLING_INTERVAL = 10
            NUM_TRIES = 0
        }
    }
    return POLLING_INTERVAL*1000
}

gotMsg = ()=> {
    POLLING_INTERVAL = 1
    this.pollAgain()
}


pollAgain = () => {
    console.log(TIMER)
    console.log(POLLING_INTERVAL)
    if(TIMER) clearTimeout(TIMER)
    TIMER = setTimeout(() => {
            this.pollForMsgs()
    }, this.currentTimeOut())
}

pollForMsgs = () => {
    this.getBotMsg((err, msg)=>{
        if(err){
            this.writeToMessages({
                from:'admin',
                text:err
            })
        }else{
            if(msg)
                this.writeToMessages(msg)
        }
        this.pollAgain()
    })
    //pollAgain()
}

getBotMsg = (cb) => {
    let url_ = this.props.conf.everlifeAvatar+'bot'
    console.log(url_)
    let xhr = new XMLHttpRequest()
    xhr.onreadystatechange = function() {
        if(xhr.readyState !== XMLHttpRequest.DONE) return
        if(xhr.status !== 200) cb(xhr)
        else {
            if(xhr.response){
                try {
                    let m = JSON.parse(xhr.response)
                    console.log(JSON.stringify(m))
                    cb(null,{
                        text:m.msg,
                        from:'admin'
                    })
                } catch(e) {
                    console.log('error')
                    cb()
                }
            } else {
                console.log('no response')
                cb()
            }
        }
    }
    xhr.open('GET', url_)
    xhr.send()
}


    sendMessageToBot = (msg) => {
        let addl = '';
        let url_ = this.props.conf.everlifeAvatar+'msg'
        let xhr = new XMLHttpRequest()
        console.log(url_)
        xhr.open('POST', url_)
        xhr.send(JSON.stringify({msg:msg,addl:addl}))
    }

    incomingMessage = (msg) => {
        this.writeToMessages(msg);
        if (msg.from === 'admin') {
            document.getElementById('messageSound').play();

            if (this.autoResponseState === 'pristine') {
                this.autoResponseState = 'canceled';
            } else if (this.autoResponseState === 'set') {
                this.autoResponseState = 'canceled';
                clearTimeout(this.autoResponseTimer);
            }
        }
    };

    writeToMessages = (msg) => {
        msg.time = new Date();
        this.setState({
            message: this.state.messages.push(msg)
        });

        if (store.enabled) {
            try {
                store.transact(this.messagesKey, function (messages) {
                    messages.push(msg);
                });
            } catch (e) {
                console.log('failed to add new message to local storage', e);
                store.set(this.messagesKey, [])
            }
        }
    }
}



