import dateFormat from 'dateformat'
import { h, Component } from 'preact';

const dayInMillis = 60 * 60 * 24 * 1000;

export default class MessageArea extends Component {

    componentDidMount() {
        window.scrollTo(0, document.body.scrollHeight);
    }

    componentDidUpdate() {
        window.scrollTo(0, document.body.scrollHeight);
    }

    render(props,{}) {
        const currentTime = new Date();
        return (
            <ol class="chat">
                {props.messages.map(({name, text, from, time}) => {
                    if (from === 'visitor') {
                        name = "You";
                    }
                    return (
                        <li class={from}>
                            <div class="msg">
                                <p dangerouslySetInnerHTML={{__html:this.getHtml(text)}}></p>
                                { (props.conf.displayMessageTime) ?
                                    <div class="time">
                                        {
                                            currentTime - new Date(time) < dayInMillis ?
                                                dateFormat(time, "HH:MM") :
                                                dateFormat(time, "m/d/yy HH:MM")
                                        }
                                    </div> 
                                    :
                                    ''
                                }
                            </div>
                        </li>
                    );
                })}
            </ol>
        );
    }

    getHtml = (msg) =>{
        console.log(msg)
        return new String(msg).replace(/&/g, '&amp;')
                .replace(/</g, '&lt;')
                .replace(/>/g, '&gt;')
                .replace(/"/g, '&quot;')
                .replace(/((?:^|\s))(\/[A-Z_a-z0-9]*)/g, `$1<a href="#" onclick="let ui = document.getElementById('everlifeuserchatinput');if(ui.value) ui.value = '$2' + ui.value;else ui.value='$2';ui.focus();return false;">$2</a>`)
                .replace(/[\r\n]/g, '<p class=br>&nbsp;</p>')
    }


}



